@import "helpers/mixins";

.law-page {
  .channelsBanner .bgGlobal:nth-child(2) {
    transform: unset;
    max-width: unset;
  }

  .defIcon {
    margin-left: 25vw;

    .txt {
      display: flex;
      flex-direction: column;
      gap: 1rem;

      h2:first-child {
        font-weight: normal;
      }
    }

    @include breakpoints(max-medium) {
      margin: auto !important;
      .def {
        flex-wrap: wrap;
        .txt {
          text-align: center;
        }
      }
    }
  }

  .welcomingSect {
    position: relative;
    margin: 5rem auto;
    .figHover {
      img {
        max-width: 30vw;

        &:nth-child(1) {
          transform: translateX(15%);
          z-index: 22;
        }
        &:nth-child(2) {
          transform: scale(1.3) translate(-15%, 16%);
        }
      }

      .txt {
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(0%, 68%);
        max-width: 25vw;
        p:not(:last-child) {
          margin-bottom: 1rem;
        }
      }

      @include breakpoints(max-medium) {
        flex-wrap: wrap;

        .txt {
          flex: 100%;
          max-width: unset;
          position: unset;
          transform: translate(0%, 50%);
        }
      }
    }
  }

  .donaMsg {
    h2 {
      margin-bottom: 2rem;
    }
    .txt {
      position: relative;
      background-color: var(--bg-clr);
      box-shadow: -5px -5px 15px grey;
      padding: 1.3rem 1.3rem 3rem;
      max-width: 90%;

      a {
        position: absolute;
        bottom: 0;
        left: 80%;
        transform: translate(-50%, 50%);
        button {
          background-color: var(--alt-clr);
        }
        &:hover button {
          background-color: white;
          color: var(--theme-clr);
        }
      }
    }
  }

  .alt-form {
    margin: 0 auto;
    .box {
      position: unset;
      background: linear-gradient(
        0deg,
        hsla(0, 0%, 69%, 1) 0%,
        hsla(0, 0%, 100%, 1) 52%
      );
      padding: 3rem 3rem 0;
      width: 80%;
      border-radius: 70px 0;
      filter: drop-shadow(0.5rem 0.5rem 0.6rem grey);

      @include breakpoints(min-xlarge) {
        max-width: 30vw;
      }

      input:not(input[type="submit"]):not(#formFile),
      textarea,
      .fileInp {
        background: linear-gradient(
          0deg,
          hsla(0, 0%, 100%, 1) 60%,
          #61c3ff40 100%
        );
        border-bottom-right-radius: 50px;
      }

      .fileInp {
        padding: 0.7rem 0.5rem 1rem;

        #formFile {
          background-color: transparent;
        }

        label {
          margin-bottom: 0.5rem;
          display: inline-block;
        }
      }

      input[type="submit"] {
        transform: translateY(50%);
        padding: 0.2rem 1rem !important;
        border-radius: 25px 0;
        background-color: var(--bg-clr);
      }

      .form textarea {
        @include breakpoints(mobile) {
          background-color: unset;
        }
      }

      .form > *:not(:last-child) {
        margin-bottom: 1rem;
      }

      .form {
        input,
        textarea {
          padding: {
            left: 1rem;
            right: 1rem;
          }
        }
      }

      .radios {
        text-align: left;
        background: linear-gradient(
          0deg,
          hsla(0, 0%, 100%, 1) 60%,
          #61c3ff40 100%
        );
        border-bottom-right-radius: 50px;
        padding: 0.6rem 1rem;
        width: 100%;

        p {
          font-size: 1.2rem;
          margin-bottom: 0.7rem;
        }
        .rad {
          &:not(:last-child) {
            margin-bottom: 0.45rem;
          }
          input[type="radio"] {
            appearance: auto;
            display: inline-block;
            margin-bottom: unset;
            width: fit-content;
          }
        }
      }
    }

    position: relative;
    &::before {
      content: "";
      position: absolute;
      left: 10vw;
      top: 10%;
      width: 150px;
      aspect-ratio: 0.9;
      z-index: -1;
      transform: rotate(30deg);
      background: {
        image: url("../assets/images/all_pages/newBgs1.webp");
        size: contain;
        repeat: no-repeat;
      }

      @media (min-width: 1800px) {
        inset: 80% 0;
      }

      @include breakpoints(mobile) {
        top: 50%;
      }
    }
  }

  section.lawPosts {
    margin: 15rem auto 0;
    .container {
      .posts {
        display: flex;
        flex-direction: column;
        gap: 4rem;
        .post {
          display: flex;
          align-items: center;

          @include breakpoints(max-medium) {
            flex-direction: column;
          }

          &:nth-child(odd) {
            @include breakpoints(min-xlarge) {
              direction: rtl;
            }

            img {
              transform: translate(30%, -20%);
            }
            .txt {
              text-align: left;

              @include breakpoints(min-large) {
                padding-left: calc(min(7vw, 7rem));
                padding-right: 1.3rem;
              }
            }
          }

          img {
            max-width: 20vw;
            transform: translate(-30%, 60%);

            @include breakpoints(min-xlarge) {
              order: 2;
            }

            @include breakpoints(large) {
              position: absolute;
              transform: translateX(-12vw) !important;
            }

            @include breakpoints(max-medium) {
              max-width: 50vw;
              transform: unset !important;
            }
          }
          .txt {
            padding: 1.3rem;
            background-color: var(--bg-clr);
            box-shadow: -5px 8px 10px #80808059;
            border-bottom-right-radius: 30px;

            @include breakpoints(large) {
              padding-left: 5rem !important;
            }

            @include breakpoints(min-xlarge) {
              padding-right: 5rem;
              width: 60%;
            }
          }
        }
      }
    }
  }

  .pagesListings {
    margin: 5rem auto;
    .container {
      .icons {
        display: flex;
        align-items: flex-end;
        width: 50vw;
        justify-content: space-around;
        margin: auto;

        @include breakpoints(max-medium) {
          align-items: center;
          flex-direction: column;
          gap: 4rem;
        }

        .box {
          display: flex;
          flex-direction: column;
          gap: 1rem;
          align-items: center;
          img {
            max-width: 120px;
          }
          p {
          }
        }
      }
    }
  }

  .swp {
    margin-bottom: 5rem;
  }
}

.law-page.arabic {
  .defIcon {
    margin: {
      right: 25vw;
    }
  }

  .donaMsg .txt {
    text-align: right;
    a {
      left: auto;
      right: 80%;
    }
  }

  .alt-form .box {
    direction: rtl;

    .radios {
      text-align: revert;
    }

    input:not(input[type="submit"]):not(#formFile),
    textarea,
    .fileInp {
      border-bottom-right-radius: unset;
      border-bottom-left-radius: 50px;
    }
  }

  .pagesListings {
    margin-top: 15rem;
  }

  section.lawPosts .container .posts .post .txt {
    text-align: right;
  }
}
